import { Component, OnInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { BehaviorSubject } from 'rxjs';

import { AppService } from '../../../services/app.service';
import { Utils } from '../../../shared/utility.class';
import { BaseComponent } from '../../../shared/globals/base.component';

import * as dayjs from 'dayjs';
@Component({
    selector: 'report',
    templateUrl: './report.template.html',
    styleUrls: ['./report.style.scss']
})
export class ReportComponent extends BaseComponent implements OnInit {

    public model: any = {
        locations: [],
        selected: []
    };
    public report$ = new BehaviorSubject([]);
    /** Whether a report has been downloaded from the server */
    public has_report: boolean;
    /** Whether a report is being loaded */
    public loading: boolean;

    /** Whether the current report has any data */
    public get has_items() {
        const list = this.report$.value || [];
        return list.length > 0
    }

    constructor(private _service: AppService, private _route: ActivatedRoute) {
        super();
    }

    public ngOnInit(): void {
        this.model.start_date = dayjs().startOf('d').valueOf();
        this.model.end_date = dayjs().endOf('d').valueOf();
        this.model.report_list = [
            {
                id: 'catering',
                name: 'Aggregated Charge Back Report',
                columns: [
                    'date',
                    'subject',
                    'host',
                    'room',
                    'catering_code',
                    'menu_category',
                    'menu_items',
                    'quantities',
                    'item_price'
                ]
            },
            {
                id: 'bookings',
                name: 'Aggregated Bookings Report',
                single_zone: true,
                columns: [
                    'start_date',
                    'end_date',
                    'subject',
                    'host',
                    'room',
                    'attendees',
                    'status',
                    'setup',
                    'breakdown',
                    'is_all_day',
                    'is_cancelled',
                    'recurrence',
                    'catering',
                    'notes'
                ]
            }
        ];
        this.subscription('route', this._route.paramMap.subscribe(params => {
            if (params.has('id')) {
                this.model.report = this.model.report_list.find(report => report.id === params.get('id'));
                this.model.columns = this.model.report.columns;
                this._service.set('APP.title', this.model.report.name);
            }
        }));
        this.has_report = false;
        this.model.building = this._service.Buildings.current();
        this.model.locations = this._service.Buildings.list() || [];
        if (this.model.building) {
            this.model.locations.forEach(i => i.selected = i.id === this.model.building.id);
            this.updateSelected();
        }
    }

    public updateLocations(location) {
        if (this.model.report.single_zone) {
            this.model.locations.forEach(i => {
                if (i !== location) { i.selected = false; }
            });
        }
        this.updateSelected();
    }

    public updateSelected() {
        this.model.selected = this.model.locations.filter(el => el.selected);
    }

    public changeStartDate(): void {
        this.model.show_calendar_start = false;
        if (this.model.start_date >= this.model.end_date) {
            this.model.end_date = dayjs(this.model.start_date).endOf('d').valueOf();
        }
    }

    public changeEndDate(): void {
        this.model.show_calendar_end = false;
        this.model.end_date = dayjs(this.model.end_date).endOf('d').valueOf();
        if (this.model.start_date > this.model.end_date) {
            this.model.start_date = dayjs(this.model.end_date).startOf('d').valueOf();
        }
    }

    public getReport(): void {
        if (this.model.selected && this.model.selected.length < 1) {
            this._service.info('Please select at least one location');
            return;
        }
        this.loading = true;
        // waiting for back end changes to accomodate report type
        if (this.model.report.id === 'bookings') {
            this._service.BookingsReports.query({
                start_date: dayjs(this.model.start_date).unix(),
                end_date: dayjs(this.model.end_date).unix(),
                zone_ids: this.model.selected.map(el => el.id)
            }).then(res => {
                res.forEach((i: any) => {
                    if (i.status) {
                        const result = i.status.replace( /([A-Z])/g, " $1" );
                        i.format_status = result.charAt(0).toUpperCase() + result.slice(1);
                    }
                });
                this.report$.next(res);
                this.loading = false;
                this.has_report = true;
            }).catch(e => {
                this.loading = false;
            });
        } else {
            this._service.Reports.query({
                start_date: dayjs(this.model.start_date).unix(),
                end_date: dayjs(this.model.end_date).unix(),
                zone_ids: this.model.selected.map(el => el.id)
            }).then(res => {
                this.report$.next(res);
                this.loading = false;
                this.has_report = true;
            }).catch(e => {
                this.loading = false;
            });
        }
    }

    public downloadFile(): void {
        const filename = `${this.model.report.name}-${dayjs().format('YYYYMMDD')}.csv`;
        const data = this.report$.value;
        const csv = Utils.generateCSV(data.map(i => {
            const output: any = {};
            this.model.report.columns.forEach(key => {
                output[key] = i[key];
            });
            if (output['item_price']) {
                output['item_price'] = (new CurrencyPipe('en-us')).transform(output['item_price'] / 100, output.symbol);
            }
            if (output['attendees']) {
                output['attendees'] = "\"" + output['attendees'].join('\n') + "\"";
            }
            if (output['notes'] && output['notes'].length > 1) {
                output['notes'] = JSON.stringify(output['notes'])
                    .replace(new RegExp(',', 'g'), 'ï¼Œ')
                    .replace(/<[^>]*>?/gm, '')
                    .replace(/\&nbsp\;/gm, ' ')
                    .replace(/\\r\\n/g, '\\n')
                    .replace(/\\n\\n/g, '\\n')
                    .replace(/\\n\\n/g, '\\n');
            } else {
                delete output['notes'];
            }
            delete output.symbol;
            return output;
        }));
        Utils.download(filename, csv);
    }

    /**
     * Get currency code for the given room
     * @param email Email of the room
     */
    private symbolForRoom(email: string) {
        const room = this._service.Rooms.item(email);
        if (room) {
            const level = room.level || {} as any;
            const building = this._service.Buildings.get(level.bld_id);
            if (building) {
                return building.currency || 'USD';
            }
        }
        return 'USD';
    }
}
