import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { WidgetsModule } from '@acaprojects/ngx-widgets';
import { ScrollingModule } from '@angular/cdk/scrolling';

import { SharedComponentModule } from '../../shared/components/shared.module';

import { AppReportsComponent } from './reports.component';
import { ReportComponent } from './report/report.component';

const ROUTES: Routes = [
    { path: '', component: AppReportsComponent },
    { path: ':id', component: ReportComponent },
    { path: '**', redirectTo: '' }
]

@NgModule({
    declarations: [
        AppReportsComponent,
        ReportComponent,
    ],
    imports: [
        CommonModule,
        FormsModule,
        SharedComponentModule,
        WidgetsModule,
        ScrollingModule,
        RouterModule.forChild(ROUTES)
    ],
    exports: [],
    providers: [],
})
export class ReportsModule {}